import React from 'react';
import "../App"
// import Screen3 from './Screen3';

export default class HeadSection extends React.Component{


    constructor(props){
        super(props);
        this.state={

        }
    }

    render(){
      var monthObj = {
        "0": "January",
        "1": "February",
        "2": "March",
        "3": "Epril",
        "4": "May",
        "5": "June",
        "6": "July",
        "7": "August",
        "8": "September",
        "9": "October",
        "10": "November",
        "11": "December",
      };
      var date = new Date()
      var currentDate =date.getDate();
      var currentMonth = date.getMonth();
      var currentYear = date.getFullYear();
      console.log(currentMonth);
      console.log(currentYear);


        return (
          <div>
            <div className="headcontainer">
              <div className="titleName">
                <h1>To-Do List</h1>
              </div>
              <div className="current">
                <span>{monthObj[currentMonth]}</span>
                <br />
                <span>{currentYear}</span>
              </div>
              <div className="box-inner">
                <div className="inner-div">
                  <span>
                    {currentDate > 3 && currentDate < 29 ? currentDate - 3 : ""}
                  </span>
                </div>
                <div className="inner-div">
                  <span>
                    {currentDate > 2 && currentDate < 30 ? currentDate - 2 : ""}
                  </span>
                </div>
                <div className="inner-div">
                  <span>
                    {currentDate > 1 && currentDate < 31 ? currentDate - 1 : ""}
                  </span>
                </div>
                <div className="middel">
                  <span>{currentDate}</span>
                </div>
                <div className="inner-div">
                  <span>
                    {currentDate < 31 && currentDate > 1 ? currentDate + 1 : ""}
                  </span>
                </div>
                <div className="inner-div">
                  <span>
                    {currentDate < 30 && currentDate > 2 ? currentDate + 2 : ""}
                  </span>
                </div>
                <div className="inner-div">
                  <span>
                    {currentDate < 29 && currentDate > 3 ? currentDate + 3 : ""}
                  </span>
                </div>
              </div>
            </div>
          </div>
        );
    }
}

