import React from "react";
import DisplayList from "./DisplayList";
import '../App'
// import Screen3 from "./Screen3";

export default class Screen2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tempTask: "",
      Tasklist: [],
      taskDetails: "",
      wholeDate:""
    };
  }

  //   With this Function Captured the input task name into temp variable
  inputSaver = (evt) => {
    var date = new Date().getDate();
    this.setState({
      tempTask: evt.target.value,
      tempDate: date,
    });
    // console.log(this.state.tempTask);
    // console.log(this.state.tempDate);
  };

  //   With this Function Captured the input date into temp variable
  dateSaver = (evt) => {
    // var inputDateArr = evt.target.value.split("-");
    var inputDateFull = new Date(evt.target.value);
      // console.log(inputDateArr);

    this.setState({
      // tempDate: Number(inputDateArr[2]),
      // tempMonth: Number(inputDateArr[1])
      wholeDate: inputDateFull,
    });
      // console.log(this.state.wholeDate)
      // console.log(new Date());
  };

  textSaver = (evt) => {
    this.setState({
      taskDetails: evt.target.value,
    });
  };

  toggle = () => {
    var blur = document.getElementById("blur");
    blur.classList.toggle("active");
    var blur2 = document.getElementById("blur2");
    blur2.classList.toggle("active");
    var blur3 = document.getElementById("blur3");
    blur3.classList.toggle("active");
    var popup = document.getElementById("popup");
    popup.classList.toggle("active");
  };

  fillList = () => {
    this.setState({
      Tasklist: [
        ...this.state.Tasklist,
        {
          Task: this.state.tempTask,
          TaskDecs: this.state.taskDetails,
          checked: false,
          DueCheckDate:this.state.wholeDate
        },
      ],
      tempTask: "",
      taskDetails: "",
    });
    this.toggle();
    console.log(this.state.tempMonth,this.state.tempDate)
  };

  toggleChecked = (index) => {
    this.setState({...this.state,Tasklist:[...this.state.Tasklist.slice(0,index),{...this.state.Tasklist[index],checked:true},...this.state.Tasklist.slice(index+1)]});
  };
  // !this.state.Tasklist[index].checked

  delFun = (val) => {
    this.setState({
      Tasklist: [
        ...this.state.Tasklist.slice(0, val),
        ...this.state.Tasklist.slice(val + 1),
      ],
    });
  };

  render() {
    console.log(this.state)
    return (
      <div>
        <DisplayList
          impTasklist={this.state.Tasklist}
          deleteItem={this.delFun}
          toggleChecked={this.toggleChecked}
        />
        <div className="screen2wrapper" id="popup">
          <div className="screen2">
            <div>
              <input
                type="text"
                onChange={this.inputSaver}
                value={this.state.tempTask}
                placeholder="Enter Task Name"
              />
            </div>
            <div className="duedate">
              <label className="duelabel" placeholder="Enter Due Date">
                Due Date
              </label>
              <input type="date" onChange={this.dateSaver} />
            </div>
            <div>
              <textarea
                placeholder="Enter Task Details"
                onChange={this.textSaver}
                value={this.state.taskDetails}
              ></textarea>
            </div>
          </div>
          <button
            className="addtolistbtn"
            onClick={this.fillList}
            title="Add Task to List"
          >
            Add to Tasklist
          </button>
        </div>
        
      </div>
    );
  }
}
