import React from "react";
import "../App";


export default class DisplayList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }


  render() {
    var color = [
      "FEE94E",
      "A9FE4E",
      "4EFE78",
      "7D4EFE",
      "EC4EFE",
      "FEA74E",
      "FEE94E",
      "A9FE4E",
      "4EFE78",
      "7D4EFE",
      "EC4EFE",
      "FEA74E",
    ];
    
    var today = new Date();
    var myDate = new Date(
      today.getFullYear(),
      today.getMonth(),
      today.getDate(),
      0,
      0,
      0
    );

    return (
      <div>
        <div className="displayListdiv" id="blur3">
          <ul>
            {this.props.impTasklist.map((item, index) => (
              <li
                key="index"
                style={
                  item.checked || item.DueCheckDate < myDate
                    ? {
                        backgroundImage: `linear-gradient(to right,  black, #fff)`,
                        border: "2px solid yellow",
                        // boxShadow: "inset 0 2px 5px black",
                      }
                    : {
                        backgroundImage: `linear-gradient(to right,  #${color[index]}, #fff)`,
                      }
                }
              >
                <span>
                  <input
                    type="checkbox"
                    onChange={() => this.props.toggleChecked(index)}
                    checked={item.checked}
                  ></input>
                  <span
                    style={
                      item.DueCheckDate < myDate
                        ? { color: "red" }
                        : { color: "black" }
                    }
                  >
                    <span
                      style={
                        item.checked
                          ? { textDecoration: "line-through", color: "red" }
                          : {}
                      }
                    >
                      {item.Task} &emsp;
                    </span>
                  </span>
                  <button
                    className="listdelbtn"
                    onClick={() => this.props.deleteItem(index)}
                  >
                    &#10006;
                  </button>
                  <div className="duedatepass">
                    {item.DueCheckDate < myDate ? (
                      <span>Due day is passed </span>
                    ) : (
                      ""
                    )}
                  </div>
                </span>
              </li>
            ))}
          </ul>
        </div>
        <div></div>
      </div>
    );
  }
}
