import React from "react";
import "../App";
// import Screen1 from './Screen1'

export default class FootSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  toggle = () => {
    var blur = document.getElementById("blur");
    blur.classList.toggle('active');
    var blur2 = document.getElementById("blur2");
    blur2.classList.toggle("active");
    var blur3 = document.getElementById("blur3");
    blur3.classList.toggle("active");
    var popup = document.getElementById("popup");
    popup.classList.toggle("active");
  };

  render() {
    return (
      <div>
        <div className="btnwrap">
          <button
            className="showscreen2btn"
            onClick={this.toggle}
            title="Add Task"
          >
            &#10010;
          </button>
        </div>
        <div className="nothingdiv"></div>
      </div>
    );
  }
}
